import { Component, Input, OnInit, OnChanges, OnDestroy, SimpleChanges, EventEmitter, Output } from '@angular/core';
import View from 'ol/View';
import { MapComponent } from './map.component';
import { ObjectEvent } from 'ol';
import { Extent } from 'ol/extent';
import { Coordinate } from 'ol/coordinate';
import { fromLonLat } from 'ol/proj';

@Component({
  selector: 'aol-view',
  template: `
    <ng-content></ng-content>
  `,
})
export class ViewComponent implements OnInit, OnChanges, OnDestroy {
  public instance: View;
  public componentType = 'view';

  @Input()
  constrainRotation: boolean | number;
  @Input()
  enableRotation: boolean;
  @Input()
  extent: Extent;
  @Input()
  maxResolution: number;
  @Input()
  minResolution: number;
  @Input()
  maxZoom: number;
  @Input()
  minZoom: number;
  @Input()
  resolution: number;
  @Input()
  resolutions: number[];
  @Input()
  rotation: number;
  @Input()
  zoom: number;
  @Input()
  zoomFactor: number;
  @Input()
  center: Coordinate;
  @Input()
  projection: string;
  @Input()
  zoomAnimation = false;

  @Output()
  onChangeZoom: EventEmitter<ObjectEvent> = new EventEmitter<ObjectEvent>();
  @Output()
  onChangeResolution: EventEmitter<ObjectEvent> = new EventEmitter<ObjectEvent>();
  @Output()
  onChangeCenter: EventEmitter<ObjectEvent> = new EventEmitter<ObjectEvent>();

  constructor(private host: MapComponent) {}

  ngOnInit() {
    // console.log('creating ol.View instance with: ', this);
    this.instance = new View(this);
    this.host.instance.setView(this.instance);

    this.instance.on('change:zoom', (event: ObjectEvent) => this.onChangeZoom.emit(event));
    this.instance.on('change:resolution', (event: ObjectEvent) => this.onChangeResolution.emit(event));
    this.instance.on('change:center', (event: ObjectEvent) => this.onChangeCenter.emit(event));
  }

  ngOnChanges(changes: SimpleChanges) {
    const properties: { [index: string]: any } = {};
    if (!this.instance) {
      return;
    }
    let args= {};
    for (const key in changes) {
      if (changes.hasOwnProperty(key)) {
        switch (key) {
            case 'rotation':
                if (this.zoomAnimation) {
                    args[key]= changes[key].currentValue;
                } else {
                    properties[key] = changes[key].currentValue;
                }            
                break;                  
            case 'zoom':
                /** Work-around: setting the zoom via setProperties does not work. */
                if (this.zoomAnimation) {
                    args[key]= changes[key].currentValue;
                } else {
                    this.instance.setZoom(changes[key].currentValue);
                }
                break;
            case 'projection':
                this.instance = new View(this);
                this.host.instance.setView(this.instance);
                break;
            default:
                properties[key] = changes[key].currentValue;
                break;
        }
        if( this.zoomAnimation && (
            typeof args['zoom']!== 'undefined' || 
                typeof args['rotation']!== 'undefined') ) {
            this.instance.animate(args);
        }
      }
    }
    //console.log('changes detected in aol-view, setting new properties: ', properties);
    this.instance.setProperties(properties, false);
  }

  ngOnDestroy() {
    // console.log('removing aol-view');
  }
}
