import { Component, OnDestroy, OnInit } from '@angular/core';
import { defaults, Interaction, DragPan, DragZoom, PinchZoom,
    MouseWheelZoom, KeyboardPan, KeyboardZoom } from 'ol/interaction';
import { Collection } from 'ol';
import { MapComponent } from '../map.component';

@Component({
  selector: 'aol-interaction-freeboard',
  template: '',
})
export class FreeboardInteractionComponent implements OnInit, OnDestroy {
    interactions: Array<Interaction>= [];

    constructor(private map: MapComponent) {}

    ngOnInit() {
        this.interactions.push(new DragPan(this));
        this.interactions.push(new DragZoom(this));
        this.interactions.push(new PinchZoom(this));
        this.interactions.push(new MouseWheelZoom(this));
        this.interactions.push(new KeyboardPan(this));
        this.interactions.push(new KeyboardZoom(this));
        this.interactions.forEach(i => this.map.instance.addInteraction(i));
    }

    ngOnDestroy() {
        this.interactions.forEach(i => this.map.instance.removeInteraction(i));
    }
}
